// main: ../style.scss
@import 'vars';
@import 'mixin'; // forms

.form-hold {
	position: relative;
	display: block;
	width: 100%;

	input[type=email],
	input[type=number],
	input[type=password],
	input[type=tel],
	input[type=text],
	select,
	textarea {
		position: relative;
		width: calc(100% - 22px);
		margin: 10px 0 0;
		padding: 10px;
		background-color: transparent;
	}

	textarea {
		width: calc(100% - 22px);
		height: 100px;
		margin: 10px 0 0;
		padding: 10px;
		resize: none;
	}

	select {
		width: 100%;
		height: 40px;
		margin: 10px 0 0;
		padding: 0 5px;
	}

	input[type=submit] {
		position: relative;
		display: inline-block;
		padding: 14px 22px;
		@include define-border-radius(24px);
		text-align: center;
		text-decoration: none !important;
		min-width: 100px;
		max-width: 250px;
		margin: 20px 0 0;
		border: none;
		cursor: pointer;
		@include define-basic-transition(all, 0.3s);
		white-space: normal;
		text-transform: uppercase;
		-webkit-appearance: none;
	}

	input[type=submit]:disabled {
		opacity: 0.7;
	}

	input[type=email]:focus,
	input[type=number]:focus,
	input[type=tel]:focus,
	input[type=text]:focus,
	select:active,
	textarea:focus {
		border-color: $color-form-ok;
		@include define-basic-transition(all, 0.3s);
	}

	.wpcf7-not-valid {
		border-color: $color-form-error !important;
	}

	.wpcf7-not-valid-tip {
		display: block !important;
		z-index: 99 !important;
		width: calc(100% - 20px) !important;
		background-color: $color-form-error !important;
		color: $color-1 !important;
		border: none !important;
		left: 0 !important;
		font-size: $cta1-size-desktop !important;
		font-weight: $font-weight-1 !important;
		text-transform: uppercase;
		padding: 10px !important;
	}
}

.wpcf7-mail-sent-ng,
.wpcf7-validation-errors {
	display: block !important;
	z-index: 99 !important;
	width: calc(100% - 40px) !important;
	background-color: $color-form-error !important;
	color: $color-1 !important;
	border: none !important;
	left: 0 !important;
	font-size: $cta1-size-desktop !important;
	font-weight: $font-weight-1 !important;
	text-transform: uppercase;
	padding: 20px !important;
}

.wpcf7-mail-sent-ok {
	display: block !important;
	z-index: 99 !important;
	width: calc(100% - 40px) !important;
	background-color: $color-form-ok !important;
	color: $color-1 !important;
	border: none !important;
	left: 0 !important;
	font-size: $cta1-size-desktop !important;
	font-weight: $font-weight-1 !important;
	text-transform: uppercase;
	padding: 20px !important;
}